ul{
    display: flex;
    justify-content: center;
    gap: 30px;

}
li{
    padding-top: 35px;
    list-style: none;
text-align: center;
width: 300px;
height: 200px;
border:solid 3px #000;
border-radius: 10px;
transition: box-shadow 500ms linear 0s;
}
li:hover{
    box-shadow: 5px 4px 14px 0.1px rgba(0, 0, 0, 0.75);
    transition: box-shadow 500ms linear 0s;
    
}
.price{

   font-weight: bold;
    color: #000;
}
p{
    font-family: 'Courier New', Courier, monospace;
}
h2{
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    color: #000;
    font-weight: bold;
}